<template>
  <div class="container" v-loading.fullscreen.lock="loading">
    <!-- ÁîµÂΩ±Â∞ÅÈù¢‰∏é‰ø°ÊÅØ -->
    <el-row v-if="!this.loading" type="flex" justify="center" align="middle">
      <!-- Â∞ÅÈù¢ -->
      <el-col :span="12" type="flex" align="middle">
        <el-image
          :src="this.mainImageUrl"
          fit="scale-down">
        </el-image>
      </el-col>
      <!-- ‰ø°ÊÅØ -->
      <el-col :span="12">
        <h1>{{movieInfo.title}}</h1>
        <p class="title2">
          ÂéüÂêçÔºö{{movieInfo.original_title}}<br>
          ÂèàÂêçÔºö{{cutData(movieInfo.aka)}}
        </p>
        <p>ÂØºÊºîÔºö{{cutData(getPersons(movieInfo.directors))}}</p>
        <p>‰∏ªÊºîÔºö{{cutData(getPersons(movieInfo.casts))}}</p>
        <p>ÁºñÂâßÔºö{{cutData(getPersons(movieInfo.writers)) || ÊöÇÊó†}}</p>
        <p>ËØÑÂàÜÔºö
          <el-rate
            style="display: inline"
            :max="10"
            :value="rating"
            disabled
            show-score
            text-color="#ff9900"
            score-template="{value}">
          </el-rate>
        </p>
        <p>Â§ßÈôÜ‰∏äÊò†Êó•ÊúüÔºö{{movieInfo.mainland_pubdate || 'Êú™‰∏äÊò†'}}</p>
        <p>ÂΩ±ÁâáÁ±ªÂûãÔºö{{cutData(movieInfo.genres)}}</p>
        <p>Âà∂ÁâáÂõΩÂÆ∂/Âú∞Âå∫Ôºö{{cutData(movieInfo.countries)}}</p>
        <p>ÁÆÄ‰ªãÔºö</p>
        <p class="summary">{{movieInfo.summary}}</p>
      </el-col>
    </el-row>
    <!-- ÂàÜÂâ≤Á∫ø -->
    <el-divider v-if="!this.loading">‰ª•‰∏ãÂΩ±ËØÑÂùá‰∏∫Áõ∏Â∫îÂΩ±ÁâáÁöÑË±ÜÁì£Áî®Êà∑ÁÉ≠ËØÑ</el-divider>
    <!-- ÂΩ±ËØÑ -->
      <ul>
        <li v-for="(review,i) in movieInfo.popular_comments" :key="i">
          <!-- ‰ΩúËÄÖÁõ∏ÂÖ≥ -->
          <div class="author">
            <!-- Â§¥ÂÉè -->
            <el-avatar
              shape="square"
              :src="review.author.avatar">
            </el-avatar>
            <span>{{review.author.name}} </span>
            <span class="smallText" style="margin-left: auto">ÂèëÂ∏ÉÊó•ÊúüÔºö{{review.created_at}}</span>
            <span class="smallText">ËØÑÂàÜÔºö‚≠ê{{review.rating.value}}</span>
            <span class="smallText">üëç {{review.useful_count}}</span>
          </div>
          <!-- ËØÑËÆ∫ÂÜÖÂÆπ -->
          <p>{{review.content}}</p>
        </li>
      </ul>
  </div>
</template>

<script>
export default {
  data () {
    return {
      id: '',
      movieInfo: [],
      mainImageUrl: '',
      loading: true,
      rating: 0
    }
  },
  methods: {
    // Êï∞ÁªÑËΩ¨‰∏∫Â≠óÁ¨¶‰∏≤Ôºå‚Äú/‚Äù ÈöîÂºÄ
    cutData: arr => {
      if (arr) {
        if (arr.length > 1) {
          return arr.join(' / ')
        }
        return arr[0]
      }
    },
    // Â∞ÜÊï∞ÁªÑ‰∏≠ÂêçÂ≠óÊèêÂèñÂá∫Êù•
    getPersons: arr => {
      if (arr) {
        let names = []
        arr.forEach(val => {
          names.push(val.name)
        })
        return names
      }
    }
  },
  mounted () {
    // Ëé∑Âèñid
    this.id = this.$route.params.id

    this.$http.get(`https://douban.uieee.com/v2/movie/subject/${this.id}`)
      .then(res => {
        this.movieInfo = res.data
        // console.log(this.movieInfo)
        // ÈáçÁΩÆÊòØÂê¶Âä†ËΩΩ
        this.loading = false
        // Ëé∑ÂèñÁîµÂΩ±ÂõæÁâá
        this.mainImageUrl = this.movieInfo.images.large
        // Ëé∑ÂèñËØÑÂàÜ
        this.rating = this.movieInfo.rating.average
      })
      .catch(err => console.log(err))
  }
}
</script>

<style scoped>
  .container{
    width: 80%;
    margin: 20px auto;
    position: relative;
  }
  .container{
    font-size: 16px;
  }
  .horizontal-center{
    position: absolute;
    margin: auto;
  }
  p{
    margin-bottom: 3px;
  }
  h1{
    font-size: 38px;
    font-weight: bolder;
  }
  .title2{
    font-size: 14px;
    color: #888;
    margin-bottom: 10px;
  }
  .summary{
    font-size: 14px;
    color: #666;
    text-indent: 2em;
  }
  .el-divider{
    background-color: #444;
  }
  .el-divider__text{
    background-color: #e4dace;
  }
  /* ÂΩ±ËØÑ */
  ul{
    padding: 0 20px;
  }
  ul li{
    margin-bottom: 10px;
    padding-bottom: 15px;
    border-bottom: 1px solid #631f16;
  }
  .author{
    margin-bottom: 10px;
    font-size: 18px;
    color: #666;
    display: flex;
    align-items: flex-end;
  }
  .smallText{
    font-size: 14px;
  }
  ul li span{
    margin-right: 10px;
  }
  ul li p{
    font-size: 16px;
    text-indent: 2em;
  }
</style>



